import { db } from "@/lib/firebase";
import { collection, getDocs, query, where } from "firebase/firestore";

interface Props {
  params: { slug: string };
}

export default async function ItemDetails({ params }: Props) {
  if (!params?.slug) {
    throw new Error("Slug parameter is missing");
  }

  const q = query(
    collection(db, "items"),
    where("slug", "==", params.slug)
  );

  const snapshot = await getDocs(q);

  if (snapshot.empty) {
    return <div className="p-10">Item not found.</div>;
  }

  const item = snapshot.docs[0].data();

  return (
    <div className="max-w-3xl mx-auto mt-10">
      <h1 className="text-3xl font-semibold mb-4">{item.name}</h1>

      <p className="text-gray-600">Slug: {item.slug}</p>

      <p className="mt-4">
        Created:{" "}
        {item.createdAt?.toDate
          ? item.createdAt.toDate().toLocaleString()
          : "â€”"}
      </p>
    </div>
  );
}